/* eslint-disable */

import * as THREE from 'three';
import OrbitControls from 'three-orbitcontrols';

import { MTLLoader } from 'three/examples/jsm/loaders/MTLLoader';
import { OBJLoader } from 'three/examples/jsm/loaders/OBJLoader';
import { TweenMax } from 'gsap';


const createTooltip = (text) => {
  var bitmap = document.createElement('canvas');
  var g = bitmap.getContext('2d');
  bitmap.width = 100;
  bitmap.height = 100;
  g.font = 'Bold 20px Arial';
  // g.textAlign = 'center';
  // g.textBaseline = 'middle';
  
  g.fillStyle = 'white';
  g.fillText(text, 0, 20);
  g.strokeStyle = 'black';
  g.strokeText(text, 0, 20);
  
  // canvas contents will be used for a texture
  var texture = new THREE.Texture(bitmap) 
  texture.needsUpdate = true;

  return texture;
}

export default () => {
  const carMesh = new THREE.Object3D();
  const doorMesh = new THREE.Object3D();
  const scene = new THREE.Scene();
  const onProgress = xhr => {
    if (xhr.lengthComputable) {
      const percentComplete = (xhr.loaded / xhr.total) * 100;
      console.log(`${Math.round(percentComplete, 2)}% downloaded`);
    }
  };

  const onError = e => {
    console.log(e);
  };

  const mtlLoader = new MTLLoader();
  mtlLoader.setPath('./images/Mustang/');
  mtlLoader.load('mustang_GT4.mtl', materials => {
    materials.preload();
    for (const key in materials.materials) {
      if (materials.materials.hasOwnProperty(key)) {
        materials.materials[key].side = THREE.DoubleSide;
      }
    }
    const objLoader = new OBJLoader();
    objLoader.setPath('./images/Mustang/');
    objLoader.setMaterials(materials);

    objLoader.load(
      'mustang_GT4.obj',
      object => {
        const door = object.children.slice(103);
        const car = object.children.splice(0, 103);

        car.forEach(p => {
          carMesh.add(p);
        });
        door.forEach(p => {
          doorMesh.add(p);
        });

        const carObj = new THREE.Object3D();
        carObj.add(carMesh);
        carObj.add(doorMesh);
        scene.add(carObj);

        console.log(carObj);
        
      },
      onProgress,
      onError
    );
  });
  const axesHelper = new THREE.AxesHelper(5);
  scene.add(axesHelper);
  const camera = new THREE.PerspectiveCamera(
    45,
    window.innerWidth / window.innerHeight,
    0.1,
    10000
  );

  const renderer = new THREE.WebGLRenderer({
    antialias: false,
  });
  renderer.setSize(window.innerWidth, window.innerHeight);
  document.body.appendChild(renderer.domElement);

  const controls = new OrbitControls(camera, renderer.domElement);
  controls.enableDamping = true;
  controls.dampingFactor = 0.25;
  controls.enableZoom = true;

  const planeGeometry = new THREE.PlaneGeometry(1000, 1000);
  const planeMaterial = new THREE.MeshBasicMaterial({
    color: 0xfbfbfb,
    side: THREE.DoubleSide,
  });
  const plane = new THREE.Mesh(planeGeometry, planeMaterial);

  const sphereGeometry = new THREE.SphereGeometry(2.5, 16, 16);
  const sphereMaterial = new THREE.MeshPhongMaterial({
    color: 0xff0000,
  });
  const sphere = new THREE.Mesh(sphereGeometry, sphereMaterial);
  const sphereNode = new THREE.Object3D();
  sphereNode.add(sphere);

  sphereNode.position.y = 25;
  sphereNode.position.x = -25;
  sphereNode.rotation.x = Math.PI / 2;

  const InfoGeometry = new THREE.PlaneGeometry(10, 10);
  const infoMaterial = new THREE.MeshPhongMaterial({
    color: 0x00ff00,
    side: THREE.DoubleSide,
    map: createTooltip('Door'),
  });
  const info = new THREE.Mesh(InfoGeometry, infoMaterial);
  const infoNode = new THREE.Object3D();
  infoNode.position.x = -25;
  infoNode.position.y = 40;

  info.rotateY(-Math.PI / 2)

  console.log(info);
  

  infoNode.add(info)
  infoNode.visible = false;
  scene.add(infoNode);


  const planeNode = new THREE.Object3D();
  planeNode.rotation.x = Math.PI / 2;

  planeNode.add(plane);
  scene.add( sphereNode );

  const pointLight = new THREE.PointLight(0xffffff, 0.5);
  camera.add(pointLight);
  scene.add(camera);
  scene.add(planeNode);

  camera.position.z = 200;
  camera.position.y = 26.5;


  renderer.render(scene, camera);

  const handleKeyDown = e => {
    if (e.key === 'w') {
      camera.position.add(new THREE.Vector3(0, 0, -5));
    }
    if (e.key === 'a') {
      camera.position.add(new THREE.Vector3(-5, 0, 0));
    }
    if (e.key === 'd') {
      camera.position.add(new THREE.Vector3(5, 0, 0));
    }
    if (e.key === 's') {
      camera.position.add(new THREE.Vector3(0, 0, 5));
    }

    if (e.key === 'q') {
      // camera.rotation.add(new THREE.Vector3(0, 0, 5));
      camera.rotation.y += 0.1;
    }

    if (e.key === 'e') {
      camera.rotation.y -= 0.1;
    }

    // console.log(sphere.position);
    sphere.geometry.geometryNeedUpdate = true;
    // doorMesh.geometry.geometryNeedUpdate = true;
  };

  var raycaster = new THREE.Raycaster();
  var mouse = new THREE.Vector2();
  const render = () => {
    raycaster.setFromCamera(mouse, camera );
  
    var intersects = raycaster.intersectObjects( [sphere] );
  
    if (intersects.length) {
      const info = document.getElementById('info');
      for ( var i = 0; i < intersects.length; i++ ) {
        if (doorMesh.visible) {
          intersects[ i ].object.material.color.set( 0x00ff00 );
          doorMesh.visible = false;
          infoNode.visible = true;

          info.classList.toggle('hidden');

          const tl = TweenMax.to(camera.position, 5, {
            x: infoNode.position.x,
            y: infoNode.position.y,
            z: infoNode.position.z,
            onUpdate: () => {
              console.log(tl.progress);
              if (tl.progress() > 0.5) {
                tl.kill();
              }
            }
          })
        } else {
          info.classList.toggle('hidden');
          intersects[ i ].object.material.color.set( 0xff0000 );
          doorMesh.visible = true;
          infoNode.visible = false;
        }
      }
    }
  }

  const onMouseClick = (event) => {
    mouse.x = ( event.clientX / window.innerWidth ) * 2 - 1;
    mouse.y = - ( event.clientY / window.innerHeight ) * 2 + 1;
    render();
  }

  var lineMaterial = new THREE.LineBasicMaterial({
    color: 0x0000ff,
    linewidth: 5,
    linecap: 'round',
    linejoin:  'round'
  });
  var lineGeometry = new THREE.Geometry();
  lineGeometry.vertices.push(sphereNode.position);
  lineGeometry.vertices.push(camera.position);
  
  
  const line = new THREE.Line( lineGeometry, lineMaterial );
  console.log(line);
  scene.add( line );

  window.addEventListener( 'click', onMouseClick, false );
  window.addEventListener('keypress', handleKeyDown);

  const animate = () => {
    // camera.lookAt(new THREE.Vector3());
    lineGeometry.vertices[1] = camera.position;
    lineGeometry.verticesNeedUpdate = true;
    renderer.render(scene, camera);
    requestAnimationFrame(animate);
  };

  animate();
  var t = 0;
  window.addEventListener('resize', () => {
    camera.aspect = window.innerWidth / window.innerHeight;
    camera.updateProjectionMatrix();

    renderer.setSize(window.innerWidth, window.innerHeight);
  });
};
